@font-face {
  font-family: montserrat-light;
  src: url(../../public/fonts/montserrat/Montserrat-Light.otf);
}

@font-face {
  font-family: montserrat-bold;
  src: url(../../public/fonts/montserrat/Montserrat-Bold.otf);
}

@font-face {
  font-family: montserrat-hairline;
  src: url(../../public/fonts/montserrat/Montserrat-Hairline.otf);
}

@font-face {
  font-family: roboto-medium;
  src: url(../../public/fonts/roboto/Roboto-Medium.ttf);
}

@font-face {
  font-family: roboto-light;
  src: url(../../public/fonts/roboto/Roboto-Light.ttf);
}

@font-face {
  font-family: roboto-bold;
  src: url(../../public/fonts/roboto/Roboto-Bold.ttf);
}

body {
  font-family: montserrat-light !important;
  color: #4c4c4c !important;
}

h1 b {
  font-family: montserrat-bold !important;
}

h2 {
  font-family: montserrat-hairline !important;
}

h2 b {
  font-family: montserrat-bold !important;
}

h3 {
  font-family: montserrat-light !important;
}

h3.font-bold {
  font-family: montserrat-bold !important;
}

h5 {
  font-family: montserrat-bold !important;
}

p b {
  font-family: montserrat-bold !important;
}

ul.list {
  padding-inline-start: 0px !important;
}

ul.list li {
  list-style: none;
}

ul.list li b {
  font-family: montserrat-bold !important;
}

ul.list li::before {
  content: "";
  display: inline-block;
  height: 25px;
  width: 25px;
  margin-right: 7px;
  background-image: url("../../public/images/svg/checklist.svg");
  background-repeat: no-repeat;
  vertical-align: bottom;
}

.nav-parent {
  border-top: solid 1px #f1f1f1;
  border-bottom: solid 1px #f1f1f1;
  padding: 10px 0px 10px 0px;
}

.nav-parent .navbar .navbar-brand {
  width: 18%;
}

.nav-parent .navbar .nav-item.last .nav-link {
  padding-right: 0rem !important;
}

.nav-parent .navbar .nav-link {
  padding-right: 1rem !important;
}

.nav-parent .navbar .nav-link:hover,
.nav-parent .navbar .nav-link .nav-link:focus {
  color: #168fee !important;
}

.navbar-light .navbar-nav .nav-link {
  color: #4c4c4c !important;
}

.jumbotron-parent {
  border-bottom: solid 1px #f1f1f1;
  margin-bottom: 50px;
}

.jumbotron-parent .jumbotron {
  background-color: #ffffff !important;
  padding: 4rem unset !important;
  margin-bottom: unset !important;
}

.jumbotron-parent .jumbotron ul {
  padding-inline-start: 15px;
}

.jumbotron-parent .jumbotron ul li {
  font-family: montserrat-hairline !important;
  margin-bottom: 10px;
}

.feature p {
  font-family: montserrat-hairline !important;
}

.card-deck .card {
  font-family: roboto-light !important;
  margin-right: unset !important;
  margin-left: unset !important;
  border-radius: 0px !important;
}

.card-deck .card sup {
  font-size: 50% !important;
}

.card-deck .card .card-body {
  text-align: center;
  padding: 0px !important;
}

.card-deck .card .card-body b {
  font-family: roboto-bold !important;
}

.card-deck .card .card-body .card-title {
  padding: 0.5rem 0px !important;
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
  font-family: roboto-bold !important;
  margin-bottom: 0px;
}

.card-deck .card .card-body .price {
  padding: 1rem 0rem;
}

.card-deck .card .card-body .price p {
  text-decoration: line-through;
}

.card-deck .card .card-body .user {
  padding: 0.5rem 0px;
  border-top: 1px solid rgba(0, 0, 0, 0.125);
  border-bottom: 1px solid rgba(0, 0, 0, 0.125);
}

.card-deck .card .card-body .list {
  padding: 2rem 0px;
}

.card-deck .card .card-body .list .btn.btn-outline-dark {
  font-size: 0.9rem !important;
}

.card-deck .card.best-seller {
  border: 1px solid #168fee;
}

.card-deck .card.best-seller .card-title {
  background: #168fee;
  background-clip: border-box;
  color: #ffffff;
}

.card-deck .card.best-seller .price {
  background: #168fee;
  background-clip: border-box;
  color: #ffffff;
}

.card-deck .card.best-seller .user {
  background: #107fde;
  background-clip: border-box;
  color: #ffffff;
}

.bg-gray {
  background-color: #f7f7f7;
}

.bg-gray .row {
  padding: 25px 0px 25px 0px;
}

.row.list-item {
  width: 80%;
  margin: 0 auto;
}

.list-group-item {
  text-align: center;
  padding: 0.5rem 1.25rem !important;
  border: 1px solid rgba(0, 0, 0, 0.05) !important;
}

.list-group-item span {
  text-align: center;
}

.list-group-item:nth-child(2) {
  background-color: #f7f7f7;
}

.list-group-item:first-child {
  border-radius: unset !important;
}

.list-group-item:last-child {
  border-radius: unset !important;
}

.list-group-item::before {
  content: "";
  display: inline-block;
  height: 25px;
  width: 25px;
  position: absolute;
  left: 10px;
  background-image: url("../../public/images/svg/checklist.svg");
  background-repeat: no-repeat;
  vertical-align: bottom;
}

hr {
  border-top: 4px solid #f1f1f1 !important;
  width: 90px;
}

.btn.btn-primary {
  background-color: #1690ef !important;
  border-color: #1690ef !important;
  padding: 10px 20px !important;
  font-family: montserrat-bold !important;
  font-size: 1.1rem !important;
}

.btn.btn-outline-dark {
  color: #4c4c4c;
  padding: 10px 30px !important;
  font-family: montserrat-bold !important;
  font-size: 1.1rem !important;
  border: 2px solid !important;
}

.btn.btn-outline-secondary {
  color: #ffffff;
  padding: 10px 30px !important;
  font-family: montserrat-light !important;
  font-size: 1.1rem !important;
  border: 2px solid #ffffff !important;
}

footer p {
  font-family: roboto-light !important;
}

footer p.title {
  font-family: roboto-medium !important;
}

footer hr {
  border-top: 1px solid #292929 !important;
  width: 100%;
}

.ribbon-parent {
  width: 110px;
  height: 110px;
  position: absolute;
  top: -10px;
  left: -10px;
  overflow: hidden;
}

.ribbon {
  margin: 0;
  text-align: center;
  background: #2fb359;
  color: white;
  padding: 0.5em 0;
  position: absolute;
  width: 200px;
  right: -25px;
  top: 30px;
  transform: rotate(-45deg);
  margin-bottom: 0;
}

.ribbon span {
  margin-left: 25px;
  font-family: roboto-bold !important;
  text-transform: uppercase;
  font-size: 14px;
}

.ribbon:before,
.ribbon .ribbon:after {
  content: "";
  position: absolute;
  top: 0;
  margin: 0 -1px;
  width: 0%;
  height: 100%;
  background: #1d7832;
}

.ribbon:before {
  right: 4px;
}

.ribbon:after {
  left: 22px;
}

.iconify {
  vertical-align: text-bottom !important;
}

@media only screen and (max-width: 600px) {
  .nav-parent .navbar .navbar-brand {
    width: 45% !important;
  }

  .bg-sky h2 {
    font-size: 20px;
  }

  .bg-sky .ml-auto {
    margin-left: unset !important;
    margin-top: 20px;
  }

  .sect .btn {
    margin-bottom: 20px;
  }
}

